import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { Location } from '@angular/common';
import {
  FormArray,
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
  FormsModule,
  ReactiveFormsModule,
} from '@angular/forms';
import {
  Client,
  ClientInfo,
  ClientsService,
  ShareClientsDataService,
  ShareStateService,
} from '@slowautomobile/exercises';
import { ActivatedRoute, Router } from '@angular/router';
import { lastValueFrom, timer, Subscription } from 'rxjs';
import { MatSnackBar } from '@angular/material/snack-bar';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { DeleteConfirmationDialogComponent } from 'libs/ui/src/lib/components/deleteConfirmationDialog/deleteConfirmationDialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatTabGroup, MatTab, MatTabContent } from '@angular/material/tabs';
import {
  PageToolbarComponent,
  ClientFullEditFormComponent,
  ClientFullEditProgramsComponent,
} from '@slowautomobile/ui';

// export interface TabNavigationTab {
//   routerLink: any;
//   label: string;
// }

@Component({
    selector: 'sanareapp-client-form',
    templateUrl: './client-form.component.html',
    styleUrl: './client-form.component.scss',
    imports: [
        PageToolbarComponent,
        ClientFullEditFormComponent,
        ClientFullEditProgramsComponent,
        FormsModule,
        ReactiveFormsModule,
        MatTabGroup,
        MatTab,
        MatTabContent,
    ]
})
export class ClientFormComponent implements OnInit, OnDestroy {
  private location = inject(Location);
  private formBuilder = inject(FormBuilder);
  private route = inject(ActivatedRoute);
  private clientsService = inject(ClientsService);
  private _snackBar = inject(MatSnackBar);
  dialog = inject(MatDialog);
  private router = inject(Router);
  private shareClientsDataService = inject(ShareClientsDataService);
  private shareStateService = inject(ShareStateService);

  subscriptions = new Subscription();

  form!: FormGroup;
  editMode = false;
  currentClientId!: string;
  // clientName!: string;
  isLoading = this.shareStateService.isLoading;

  clientInfo = this.shareClientsDataService.clientInfo;

  ngOnInit(): void {
    this._initForm();
    this._checkEditMode();

    // this.form.valueChanges.subscribe(value => console.log(value));
  }

  private _initForm() {
    this.form = this.formBuilder.group({
      clientInfo: this.formBuilder.group({
        id: [''],
        name: [''],
        firstName: ['', Validators.required],
        lastName: ['', Validators.required],
        email: ['', Validators.required],
        phone: ['', Validators.required],
        address: this.formBuilder.group({
          street: [''],
          streetNumber: [''],
          city: [''],
          zipCode: [''],
          country: [''],
        }),
      }),
      programs: this.formBuilder.array([]),
    });
  }

  get clientForm() {
    return this.form.controls;
  }

  private _checkEditMode() {
    // const subscription = this.route.params.subscribe(params => {

    // Check for ClientId param
    this.currentClientId = this.route.snapshot.paramMap.get('id') as string;

    // Check for clientName query(or other queries)
    // IT SHOULD BE REFACTORED IN NEW-PROGRAM-STEPPER, TO PASS QUERY IN PARAMS WITH NAVIGATION !~!!! UNTIL THEN, SIGNALS ARE USED(clientInfo())
    // this.clientName = this.route.snapshot.queryParamMap.get(
    //   'clientName',
    // ) as string;

    if (this.currentClientId) {
      this.editMode = true;
      // this.currentClientId = this.currentClientId;

      this.clientsService.getClient(this.currentClientId).subscribe({
        next: client => {
          console.log(client);
          this.form.get('clientInfo.id')?.setValue(this.currentClientId);
          this.form.get('clientInfo.name')?.setValue(client.name);
          this.form.get('clientInfo.firstName')?.setValue(client.firstName);
          this.form.get('clientInfo.lastName')?.setValue(client.lastName);
          this.form.get('clientInfo.email')?.setValue(client.email);
          this.form.get('clientInfo.phone')?.setValue(client.phone);
          this.form
            .get('clientInfo.address.street')
            ?.setValue(client.address?.street);
          this.form
            .get('clientInfo.address.streetNumber')
            ?.setValue(client.address?.streetNumber);
          this.form
            .get('clientInfo.address.city')
            ?.setValue(client.address?.city);
          this.form
            .get('clientInfo.address.zipCode')
            ?.setValue(client.address?.zipCode);
          this.form
            .get('clientInfo.address.country')
            ?.setValue(client.address?.country);

          // this.clientForm['name'].setValue(client.name);
          // this.clientForm['firstName'].setValue(client.firstName);
          // this.clientForm['lastName'].setValue(client.lastName);
          // this.clientForm['email'].setValue(client.email);
          // this.clientForm['phone'].setValue(client.phone);

          const programsArray = this.form.get('programs') as FormArray;
          client.programs?.forEach(program => {
            programsArray.push(new FormControl(program));
          });

          // If something goes wrong, comment this out!
          // Use case: When reusing program from client1 to client2, clientId is set to client2, so that when New btn clicked(for creating brand new program for client), then setting this clientId used to automatically set selected client
          // this.clientInfo.update(clientInfo => {
          //   return {
          //     clientId: this.currentClientId,
          //     clientName: clientInfo.clientName,
          //     programPosition: clientInfo.programPosition,
          //     programId: clientInfo.programId,
          //     programName: clientInfo.programName,
          //     dateRange: clientInfo.dateRange,
          //   };
          // });
          // On manual page reload, this will populate basic info for page functioning (clientId and clientName)
          if (!this.clientInfo().clientName) {
            this.clientInfo.update((currentValue: ClientInfo) => ({
              ...currentValue,
              clientId: this.currentClientId,
              clientName: this.form.get('clientInfo.name')?.value,
            }));
          }
        },
        error: e => {
          // Error handling logic
          // error Snackbar is needed here and elsewhere!!!!!!
          console.log(e);
          this.router.navigateByUrl('clients');
        },
      });
    }
    // });

    // this.subscriptions.add(subscription);
  }

  onSubmit() {
    // this.isSubmitted = true;

    if (this.form.invalid) {
      this._snackBar.open(
        $localize`:@@Snackbar.enterRequiredFields:Please enter required fields`,
        '',
        {
          duration: 2000,
          panelClass: ['custom-snackbar-style'],
        },
      );
      return;
    }
    this.isLoading.set(true);

    const clientForm: Client = {
      name: this.form.get('clientInfo.name')?.value,
      firstName: this.form.get('clientInfo.firstName')?.value,
      lastName: this.form.get('clientInfo.lastName')?.value,
      email: this.form.get('clientInfo.email')?.value,
      phone: this.form.get('clientInfo.phone')?.value,
      address: {
        street: this.form.get('clientInfo.address.street')?.value,
        streetNumber: this.form.get('clientInfo.address.streetNumber')?.value,
        city: this.form.get('clientInfo.address.city')?.value,
        zipCode: this.form.get('clientInfo.address.zipCode')?.value,
        country: this.form.get('clientInfo.address.country')?.value,
      },
      programs: this.form.get('programs')?.value,
    };

    console.log(clientForm);

    if (this.editMode) {
      this._updateClient(clientForm, this.currentClientId);
    } else {
      this._createClient(clientForm);
    }
  }

  private _createClient(client: Client) {
    const subscription = this.clientsService
      .createClient(client)
      .subscribe(client => {
        this._snackBar.open(
          $localize`:@@Snackbar.clientCreated:Client created`,
          '',
          {
            duration: 2000,
            panelClass: ['custom-snackbar-style'],
          },
        );

        // this.router.navigateByUrl(`clients/form/${clientId}`);
        lastValueFrom(timer(1000)).then(() => {
          this.isLoading.set(false);
          this.clientInfo.set({
            clientId: client.id as string,
            clientName: client.name,
            programPosition: 0,
            programId: '',
            programName: '',
            dateRange: { start: '', end: '' },
          });
          this.router.navigateByUrl(`clients/form/${client.id}`);
        });
      });

    this.subscriptions.add(subscription);
  }

  private _updateClient(client: Client, currentClientId: string) {
    const subscription = this.clientsService
      .updateClient(client, currentClientId)
      .subscribe({
        next: createdClient => {
          console.log(createdClient);
          this._snackBar.open(
            $localize`:@@Snackbar.clientUpdated:Client updated`,
            '',
            {
              duration: 2000,
              panelClass: ['custom-snackbar-style'],
            },
          );
          lastValueFrom(timer(1000)).then(() => {
            this.isLoading.set(false);
            this.router.navigateByUrl(`clients`);
          });
        },
        error: e => {
          console.error(e);
          this.isLoading.set(false);
          // this.router.navigateByUrl(`clients`);
        },
        complete: () => console.info('Client updated'),
      });

    this.subscriptions.add(subscription);
  }

  openDeleteConfirmationDialog(clientId: string): void {
    console.log(clientId);
    const dialogRef = this.dialog.open(DeleteConfirmationDialogComponent, {
      width: '312px',
      data: {
        title: $localize`:@@DeleteConfirmationDialog.deleteTitleClient:Delete selected client?`,
        message: $localize`:@@DeleteConfirmationDialog.deleteMessageClient:Deleting the selected client will remove it from your account.`,
      },
      panelClass: 'custom-dialog',
    });

    const subscription = dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // User confirmed deletion, handle deletion logic here
        console.log('User confirmed deletion');
        this.deleteClient(clientId);
        dialogRef.close();
      } else {
        // User canceled deletion
        console.log('User canceled deletion');
        dialogRef.close();
      }
    });

    this.subscriptions.add(subscription);
  }

  deleteClient(clientId: string) {
    const subscription = this.clientsService.deleteClient(clientId).subscribe({
      next: () => {
        this._snackBar.open(
          $localize`:@@Snackbar.clientDeleted:Client deleted`,
          '',
          {
            duration: 2000,
            panelClass: ['custom-snackbar-style'],
          },
        );
        lastValueFrom(timer(2000)).then(() => {
          this.router.navigateByUrl(`clients`);
        });
      },
      error: e => console.error(e),
      complete: () => console.info('Client deletion completed'),
    });

    this.subscriptions.add(subscription);

    // this.subscriptions.add(deleteClientSubscription);
  }

  onBack() {
    this.router.navigateByUrl(`clients`);
  }

  // tabs: TabNavigationTab[] = [
  //   { routerLink: 'general', label: 'General' },
  //   { routerLink: 'programs', label: 'Programs' },
  // ];
  // activeLink = this.tabs[0].routerLink;

  // tabs: TabNavigationTab[];

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
